// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// подключения компонентов страницы
@import "./components/header";
@import "./components/burger";
@import "./components/panels";

// тестовые стили, удалите их
main {
  padding-top: 110px;

}

.projectSection .btnb.open .panels__menu-arrow {
  transform: rotate(90deg);
}

.projectSection .btnb.open {
  color: #000;
}

@include tabs;

.btnb {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 700;
  font-size: 18px;
  line-height: 160%;
  text-align: center;
  color: #7A83A2;
  padding: 20px;

  img {
    margin-right: 30px;
  }

  .panels__menu-arrow {
    transform: rotate(180deg);
  }
}

.page__body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: var(--vh);
}

.title {
  font-weight: 700;
  font-size: 32px;
  line-height: 140%;
  color: #29304B;
}

.icon {
  @include flex-all-center;
  width: 24px;
  height: 24px;
  background: #FFFFFF;
  border: 1px solid #EBEEF7;
  border-radius: 10px;
}

.social {
  &-list {
    @include flex-v-center;
  }

  &-item {
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }
  }
}

.wrapper {
  display: flex;
  justify-content: space-between;
}


.info {
  width: 100%;
  background: #f5f7fa;
  padding: 23px;
}

.services {
  padding: 20px 0 40px 0;

  &__title {
    margin-bottom: 10px;
  }

  &__subtitle {
    font-size: 16px;
    line-height: 160%;
    color: #626983;
    margin-bottom: 15px;
  }

  &__items {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__itemms {
    display: flex;
    justify-content: space-between;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    min-height: 223px;
    width: 100%;
    border: 2px solid #EBEEF7;
    border-radius: 30px;
    margin-right: 15px;
    background-color: #FFFFFF;
    position: relative;
    padding: 30px;
    transition: all .4s linear;
    cursor: pointer;


    &:hover {
      border: 2px solid #BAC1DA;

      .btn {
        background: #304173;
        color: #fff;
      }

      &::before {
        content: '';
        position: absolute;
        top: 24px;
        left: -5px;
        background: #E16446;
        border-radius: 0px 5px 5px 0px;
        transform: rotate(180deg);
        height: 50px;
        width: 5px;
      }
    }

    &:last-child {
      margin-right: 0;
    }

    &--ones {
      background-image: url(../img/services-1.jpg);
      background-repeat: no-repeat;
      background-position: right bottom;

    }

    &--twos {
      background-image: url(../img/services-2.jpg);
      background-repeat: no-repeat;
      background-position: right bottom;
    }

    &--card {
      width: 100%;
      background-color: #FFFFFF;
      background-position: right bottom !important;
    }



    &-btn {
      color: #304173;
    }

    &-title {
      font-weight: 700;
      font-size: 24px;
      line-height: 110%;
      color: #29304B;
      margin-bottom: 20px;
    }

    &-text {
      width: 100%;
      max-width: 350px;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: #626983;
      margin-bottom: 20px;
    }

    &--one {
      background-image: url(../img/card-1.jpg);
      background-repeat: no-repeat;
    }

    &--two {
      background-image: url(../img/card-2.jpg);
      background-repeat: no-repeat;
    }

    &--thre {
      background-image: url(../img/card-3.jpg);
      background-repeat: no-repeat;
    }

    &--four {
      background-image: url(../img/card-4.jpg);
      background-repeat: no-repeat;
    }

  }
}

.doctors {
  padding: 40px 0 20px 0;

  &__top {
    @include flex-all-sb;
    margin-bottom: 20px;
  }

  &__btns {
    @include flex-all-center;
  }

  &__content {
    margin-bottom: 20px;
  }

  &__item {
    width: 100%;
    max-width: 284px;
    background: #FFFFFF;
    border: 2px solid #EBEEF7;
    border-radius: 30px;
    padding: 15px;
    transition: all .2s linear;
    position: relative;
    display: block;
    margin: 0 auto;

    &:hover {
      border: 2px solid #BAC1DA;

      &::before {
        content: "";
        position: absolute;
        top: 23px;
        left: -5px;
        background: #E16446;
        border-radius: 0px 5px 5px 0px;
        -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
        transform: rotate(180deg);
        height: 50px;
        width: 5px;
      }
    }

    &-overtitle {
      font-size: 12px;
      line-height: 140%;
      color: #7A83A2;
      margin-bottom: 7px;
      margin-top: 15px;
    }

    &-title {
      font-size: 16px;
      line-height: 160%;
      color: #29304B;
      font-weight: 700;
    }

    &-img {
      width: 100%;
    }

  }

  &__slider-btn {
    width: 30px;
    height: 30px;
    background: #fff;
    border: 2px solid #EBEEF7;
    border-radius: 50%;
    transition: all .2s linear;
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }

    &:hover {
      background: #304173;
      border: 2px solid transparent;

      .svg {
        fill: #fff;
        stroke: #fff;
      }
    }

  }

  &__button {
    justify-content: flex-end;
    display: flex;

    &-btn {
      display: inline-block;
    }
  }
}

.disease {
  &__title {
    margin-bottom: 20px;
  }

  &__button {
    @include flex-all-sb;
    text-align: center;
    flex-wrap: wrap;
    margin-bottom: 20px;

    &-item {
      @include flex-all-sb;

      .btn-bg {
        border-radius: 15px 0px 0px 15px;
      }

      .btn {
        border-radius: 0px 15px 15px 0px;
      }
    }

  }

  &__content {
    background: #FFFFFF;
    border-radius: 30px;
    padding: 20px;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(4, 2fr);
    grid-gap: 15px;
    background: #F7F9FC;
    border: 1px solid #EBEEF7;
    border-radius: 30px;
    padding: 20px;
  }

  &__item {
    @include flex-all-sb;
    width: 100%;
    background: #FFFFFF;
    border: 2px solid #EBEEF7;
    border-radius: 25px;
    transition: all .2s linear;
    position: relative;
    padding: 12px;

    &:hover {
      border: 2px solid #BAC1DA;

      &::before {
        content: "";
        position: absolute;
        top: 22px;
        left: -5px;
        background: #E16446;
        border-radius: 0px 5px 5px 0px;
        -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
        transform: rotate(180deg);
        height: 35px;
        width: 5px;
      }

      .svg {
        fill: #fff;
        stroke: #fff;
      }

      .icon {
        background: #304173;
      }
    }

    &-left {
      display: flex;
      align-items: center;
    }

    &-img {
      margin-right: 20px;
    }

    &-title {
      font-weight: 700;
      font-size: 18px;
      line-height: 160%;
      color: #29304B;
    }

    &-descr {
      font-size: 14px;
      line-height: 150%;
      color: #626983;
    }
  }
}

.faq {
  padding-bottom: 48px;

  &__title {
    text-transform: uppercase;
    margin-bottom: 20px;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
  }

  &__nav {
    width: 30%;
    display: block;
    background: #FFFFFF;
    border-radius: 20px;
    margin-right: 35px;

    &-item {
      position: relative;
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-btn {
      @include flex-v-center;
      width: 100%;
      font-weight: 700;
      font-size: 16px;
      line-height: 160%;
      color: #29304B;
      border: 2px solid transparent;
      text-align: left;
      padding: 14px 20px 14px 20px;
      transition: all .1s linear;
      position: relative;

      &:hover {
        border: 2px solid #BAC1DA;
        border-radius: 20px;

        &::before {
          content: '';
          position: absolute;
          top: 14px;
          left: -5px;
          background: #E16446;
          border-radius: 0px 5px 5px 0px;
          -webkit-transform: rotate(180deg);
          -ms-transform: rotate(180deg);
          transform: rotate(180deg);
          height: 35px;
          width: 5px;
        }
      }
    }

    &-span {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 34px;
      height: 34px;
      border: 2px solid #7292f280;
      border-radius: 50%;
      margin-right: 20px;
    }
  }

  &__info {
    width: 70%;

    &-title {
      font-weight: 700;
      font-size: 18px;
      line-height: 160%;
      color: #29304B;
      margin-bottom: 20px;
    }

    &-content {
      background: rgba(247, 249, 252, 0.5);
      border: 2px solid #7A83A2;
      border-radius: 20px;
      padding: 15px;
      margin-bottom: 20px;
    }

    &-text {
      display: block;
      font-size: 14px;
      line-height: 150%;
      color: #29304B;
      margin-bottom: 15px;
    }

    &-line {
      display: block;
      width: 100%;
      border-bottom: 1px dashed #626983;
      margin-bottom: 15px;
    }

    &-name {
      font-weight: 700;
      font-size: 14px;
      line-height: 160%;
      color: #29304B;
      margin-bottom: 8px;
    }

    &-item {
      font-size: 12px;
      line-height: 140%;
      color: #29304B;
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 0;
      }
    }

  }

  .tabs__nav-btn--active {
    background-color: transparent;
    color: #29304B;
    border: 2px solid #BAC1DA;
    border-radius: 20px;

    .faq__nav-span {
      background: #304173;
    }

    .svg {
      fill: #fff;
    }

    &::before {
      content: "";
      position: absolute;
      top: 14px;
      left: -5px;
      background: #E16446;
      border-radius: 0px 5px 5px 0px;
      -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
      transform: rotate(180deg);
      height: 35px;
      width: 5px;
    }
  }

  &__button {
    justify-content: flex-end;
    display: flex;

    &-btn {
      display: inline-block;
    }
  }
}

.mails {
  background: url(../img/mails-bg.jpg) no-repeat center / cover;
  padding: 33px 33px 33px 70px;

  &__title {
    font-size: 24px;
    font-weight: 700;
    line-height: 110%;
    display: flex;
    align-items: center;
    color: #29304B;
    margin-bottom: 20px;
  }

  &__descr {
    width: 100%;
    max-width: 424px;
    font-size: 16px;
    line-height: 140%;
    color: #29304B;
    margin-bottom: 20px;
  }

  &__form {
    width: 100%;
    max-width: 650px;

    &-label {
      display: flex;
      align-items: center;
    }

    &-input {
      width: 100%;
      max-width: 400px;
      height: 45px;
      background: #F7F9FC;
      border-radius: 15px 0px 0px 15px;
      font-size: 14px;
      line-height: 150%;
      color: #8C9BC0;
      padding-left: 15px;

      &::placeholder {
        font-size: 14px;
        line-height: 150%;
        color: #8C9BC0;
      }
    }

    &-btn {
      width: 100%;
      max-width: 155px;
      height: 45px;
      background: #304173;
      border-radius: 0px 15px 15px 0px;
    }
  }
}

.footer {
  padding-top: 90px;

  &__inner {
    @include flex-all-sb;
    margin-bottom: 20px;
  }

  &__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: #29304B;
    margin-right: 30px;
  }

  &__top {
    display: flex;
    justify-content: space-between;
  }

  &__services {
    width: 100%;
    max-width: 260px;

    &-title {
      font-weight: 700;
      font-size: 18px;
      line-height: 160%;
      color: #2C405A;
      margin-bottom: 15px;
    }

    &-descr {
      font-size: 14px;
      line-height: 140%;
      color: #626983;
      margin-bottom: 15px;
    }

    &-phone {
      font-weight: 700;
      font-size: 18px;
      line-height: 160%;
      text-align: center;
      color: #29304B;
      margin-bottom: 5px;
    }

    &-mail {
      font-size: 14px;
      line-height: 140%;
      color: #29304B;
    }
  }

  &__nav {
    width: 100%;
    max-width: 200px;
    border-right: 1px solid #EBEEF7;

    &-title {
      font-size: 18px;
      font-weight: 700;
      line-height: 160%;
      color: #2C405A;
      margin-bottom: 30px;
    }

    &-list {
      padding-left: 10px;
    }

    &-item {
      margin-bottom: 24px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-link {
      @include flex-v-center;
      font-size: 14px;
      line-height: 150%;
      color: #626983;

      span {
        margin-right: 10px;
      }
    }
  }

  &__bottom {
    @include flex-all-sb;
    margin: 50px 0 20px 0;
  }

  &__social {
    @include flex-v-center;

    &-title {
      font-weight: 700;
      font-size: 14px;
      line-height: 160%;
      color: #29304B;
      margin-right: 15px;
    }

    &-list {
      @include flex-v-center;
    }

    &-item {
      margin-right: 10px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__information {
    @include flex-v-center;

    &-link {
      font-weight: 700;
      font-size: 14px;
      line-height: 160%;
      color: #29304B;
      margin-right: 40px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__copy {
    display: flex;
    justify-content: space-between;

    &-text {
      width: 50%;
      font-size: 12px;
      line-height: 140%;
      color: #7A83A2;
    }
  }
}

