@import "media";


.burger {
  @include burger;
}

.navbar-primary {
  background-color: #fff;
  bottom: 0px;
  left: 0px;
  top: 51px;
  width: 400px;
  height: 100vh;
  z-index: 8;
  overflow: hidden;
  -webkit-transition: all 0.1s ease-in-out;
  -moz-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}

.navbar-primary.collapsed {
  width: 80px;
}
.navbar-primary.collapsed .panels__menu-kinks {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;

}

.navbar-primary.collapsed .panels__menu-arrow{
  display: none;
}
.navbar-primary.collapsed .glyphicon {
  margin-right: 0;
}
.navbar-primary.collapsed .panels__menu-arrow--item{
  transform: rotate(0deg);
  display: flex;
}
.navbar-primary.collapsed .panels__menu-svg{
  display: none;
}

.navbar-primary.collapsed .panels__social{
  display: none;
}



.navbar-primary.collapsed .nav-label {
  display: none;
}
.navbar-primary.collapsed .panels__nav {
  margin-bottom: 450px;
}

.btn-expand-collapse {
  display: block;
  left: 0px;
  bottom: 0;
  width: 100%;
  padding: 8px 0;
  color: grey;
  font-size: 20px;
  text-align: center;
  background: #000;
}





.navbar-primary-menu,
.navbar-primary-menu li {
  list-style: none;


}

.navbar-primary-menu .panels__menu-kinks {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 100%;
  font-weight: 700;
  font-size: 18px;
  line-height: 160%;
  color: #29304B;
  background: #FFFFFF;
  height: 70px;
  margin-bottom: 4px;
  padding-left: 30px;
  padding-right: 30px;

  &:last-child {
    margin-bottom: 0;
    color: #7A83A1;
  }

  &:hover {
    background: #365ED7;
    border-radius: 20px;
    color: #fff;
    .svg{
      stroke: #fff;
    }
  }
}
.panels__menu-svg{
  display: flex;
  margin-right: 10px;
}
.glyphicon{
  margin-right: 10px;
}
