@media (max-width: (1850px)){
  .panels{
    display: none;
  }
  .info{
    max-width: 100%;
  }
}
@include laptop{
  .header{
    &__logo{
      margin-right: 20px;
    }
  }
}
@include tablet {
  .services {
    &__itemms {
      display: grid;
      grid-template-columns: repeat(2, 2fr);
      gap: 10px;
    }
  }

  .faq {
    &__nav {
      width: 40%;
    }
  }
}

@include tablet-l {
  .header{
    &__menu{
      display: none;
    }

  }
  .burger{
    display: block;
  }
  .footer {
    &__top {
      display: grid;
      grid-template-columns: repeat(3, 2fr);
      gap: 10px;
    }
  }
}

@media (max-width: (837px)) {
  .disease {
    &__button {
      justify-content: space-around;

      &-item {
        margin-bottom: 10px;
      }

      &-btn {
        margin-bottom: 10px;
        font-size: 15px;
      }
    }
  }
}

@media (max-width: (830px)) {
  .disease {
    &__items {
      grid-template-columns: repeat(3, 2fr);
    }
  }

  .faq {
    &__inner {
      flex-wrap: wrap;
    }

    &__nav {
      width: 100%;
      margin-right: 0;
      height: 134px;
      overflow-x: hidden;
    }

    &__info {
      width: 100%;
    }

    &__button {
      justify-content: center;
    }
  }

  .doctors {
    &__button {
      justify-content: center;
    }
  }

}

@media (max-width: (630px)) {}

@include small-tablet {
  .disease {
    &__items {
      grid-template-columns: repeat(1, 8fr);
    }
  }

  .mails {
    padding: 10px;
  }

  .footer {
    &__top {
      display: grid;
      grid-template-columns: repeat(2, 3fr);
      gap: 10px;
    }
  }
  .footer{
    &__bottom{
      flex-direction: column;
    }
    &__social{
      margin-bottom: 20px;
    }
  }
}
@include mobile-l {
  .title {
    font-size: 25px;
  }

  .services {

    &__items {
      flex-wrap: wrap;
    }
  }
  .footer {
    &__top {
      display: grid;
      grid-template-columns: repeat(1, 3fr);
      gap: 10px;
    }
    &__inner{
      flex-direction: column;
      justify-content: start;
      align-items: start;
    }
  }
}
@include mobile {
  .services {
    &__itemms {
      grid-template-columns: repeat(1, 4fr);
    }

    &__items {
      grid-template-columns: repeat(1, 2fr);
      gap: 10px;
    }

    &__item {
      margin-right: 0;
      padding: 15px;
    }
  }
  .footer{
    &__social{
      flex-direction: column;
    }
    &__copy{
      flex-direction: column;
      &-text{
        width: 100%;
      }
    }
    &__information{
      flex-direction: column;
      text-align: center;
      &-link{
        margin-right: 0;
      }
    }
  }
  @include mobile-m{
    .disease{
      &__content{
        padding: 10px;
      }
      &__items{
        padding: 10px;
      }
    }
  }
}


