.burger{
  display: none;
  z-index: 1000;
}
.menu--active{
  width: 320px;
  height: 100vh;
  display: block !important;
  position: absolute;
  top: 80px;
  left: 0;
  z-index: 1000;
  background: #fff;
  padding: 30px;
  transition: all .4s linear;
  .header__search,
  .header__select{
    margin-bottom: 20px;
  }
}
