.panels {


  &__menu {

    &-item {
      padding: 20px;
      background: #FFFFFF;
      border-radius: 20px;
      transition: all .2s linear;

      &:hover {
        background: #365ED7;

        a,
        span {
          color: #fff !important;
        }

      }

    }

    &-btn {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-weight: 700;
      font-size: 18px;
      line-height: 160%;
      color: #7A83A2;


      img {
        margin-right: 30px;
      }
    }

    &-title {
      color: #29304B;
    }

    &-arrow {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 24px;
      height: 24px;
      background: #FFFFFF;
      border: 1px solid #EBEEF7;
      border-radius: 10px;
      margin-right: 0;
      margin-left: auto;
      &--item{
        transform: rotate(180deg);
      }
    }

    &-point {
      @include flex-all-center;
    }
  }

  &__social {
    width: 100%;
    max-width: 350px;
    background: #F5F7FA;
    border: 1px solid #EBEEF7;
    border-radius: 30px;
    margin: 0 auto;
    padding: 22px;
    &-descr {
      font-weight: 700;
      font-size: 16px;
      line-height: 140%;
      color: #29304B;
      margin-bottom: 20px;
    }
    &-list{
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 20px;
    }
  }


}
.panels__social-list{
  background: transparent;
}
