.header {
  position: fixed;
  width: 100%;
  max-width: 1920px;
  top: 0;
  z-index: 1000;
  background: #fff;
  padding: 20px 15px;

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    margin-right: 200px;
  }

  &__menu {
    width: 100%;
    display: flex;
    justify-content: center;
    justify-content: space-between;
    align-items: center;
  }

  &__search {
    width: 100%;
    max-width: 834px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 20px;

    &-label {
      width: 100%;
      position: relative;
      background: #F7F9FC;
      border-radius: 15px;
      padding: 12px 10px;

      &::before {
        content: '';
        background: url(../../img/svg/search.svg) no-repeat;
        width: 17px;
        height: 17px;
        position: absolute;
        top: 13px;
      }
    }

    &-btn {
      background: #304173;
      margin-left: 10px;
    }

    &-input {
      width: 100%;
      max-width: 610px;
      font-weight: 400;
      font-size: 14px;
      color: #7A83A2;
      padding-left: 26px;

      &::placeholder {
        font-weight: 400;
        font-size: 14px;
        color: #7A83A2;
      }
    }
  }
  &__buttons{
    display: flex;
    align-items: center;
  }
  &__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: #29304B;
    margin-right: 30px;
  }

  &__select {
    width: 100%;
    max-width: 279px;
    background: #FFFFFF;
    border-radius: 15px;
    border: 2px solid #EBEEF6;
    padding: 15px 12px;
    margin-right: 20px;
    position: relative;
  
  }


}
